import uniNavBar from '../../common/uni-nav-bar/uni-nav-bar.vue';
import newsList from '../../common/common/newsList.vue';
import loadMore from '../../common/common/loadmore.vue';
export default {
	components: {
		uniNavBar,
		newsList,
		loadMore
	},
	data() {
		return {
			tabBarIndex: 1,
			swiperHeigh: 500,
			tabBars: [
				{ name: '话题', id: 'hauti' },
				{
					name: '关注',
					id: 'guanzhu'
				}
			],
			banners:[
				{src:'../../static/demo/banner1.jpg'},
				{src:'../../static/demo/banner2.jpg'},
				{src:'../../static/demo/banner1.jpg'}
			],
			hotPic:[
				'最新',
				'游戏',
				'情感',
				'最新',
				'游戏',
				'情感'
			],
			newsList: 
				{
					loadingMore: '上拉加载更多',
					list: [{ id: 1 }, { id: 1 }, { id: 1 }, { id: 1 }, { id: 1 }, { id: 1 }]
				}
			
		};
	},

	onLoad() {
		this.getScrollHight();
	},
	methods: {
		getScrollHight() {
			uni.getSystemInfo({
				success: res => {
					this.swiperHeigh = res.windowHeight - uni.upx2px(140);
				}
			});
		},
		//tabbar click
		changeTabIndex(index) {
			this.tabBarIndex = index;
		},
		gotoAdd() {
			uni.navigateTo({
				url: '../addIndex/addIndex'
			});
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.tabBarIndex = index;
		},
		//加载更多
		loadMore(){
			let moreIndex = this.newsList.loadingMore;
			if (moreIndex != '上拉加载更多') {
				return;
			}
			this.newsList.loadingMore = '加载中......';
			setTimeout(() => {
				let obj = {
					shareNum: 20
				};
				this.newsList.list.push(obj);
				this.newsList.loadingMore = '上拉加载更多';
			}, 1000);
		}
	}
};
